import axios from 'axios'
import xml2js from 'xml2js'

let problems = null;

const fetchProblems = (done) => {
    if (problems === null) {
        axios.get("/cp2/problems.xml")
        .then(xml => {
            xml2js.parseString(xml.data, (err, res) => {
                res = res.Problems.Problem;
                problems = []
                for (let i=0; i<res.length; i++) {
                    problems.push({
                        key: i,
                        name: res[i].Name[0],
                        source: res[i].Source[0],
                        editorial: res[i].Editorial[0],
                        description: res[i].Description[0],
                        code: res[i].Code[0]
                    })
                }
                done(problems);
            });
        })
        .catch(e => {
            alert(e);
        });
    } else {
        console.log("vola!");
        done(problems);
    }
}

const fetchSingleProblem = (id) => {
    return (problems === null ? null : problems[id])
}

export { fetchProblems, fetchSingleProblem }