import React, { Component } from 'react'

import Paper from '@material-ui/core/Paper'
import Typography from '@material-ui/core/Typography'
import CodeIcon from '@material-ui/icons/Code'
import Button from '@material-ui/core/Button'
import Divider from '@material-ui/core/Divider'

import { fetchSingleProblem } from './store/store'

export class ProblemView extends Component {
  state = {
      showCode: false
  }

  toggle = (e) => {
      // TODO: fix this
      this.setState({showCode: !this.state.showCode});
  }

  render() {
    const problem = fetchSingleProblem(this.props.match.params.problem_id)
    const { name, source, description, editorial, code } = problem
    const { showCode } = this.state;

    return (
      <Paper>
        <Typography variant="title">{name}</Typography>
        <Typography variant="subtitle2">{source}</Typography>
        <Divider variant="fullWidth"/>
        <Typography component="p">{description}</Typography>
        <Divider variant="fullWidth"/>
        <Typography component="p">{editorial}</Typography>
        <Divider variant="fullWidth"/>
        <Button variant="raised" color="primary" onClick={this.toggle}>
            <CodeIcon/>{showCode ? "Hide Code" : "Show Code"}
        </Button>
       
        {showCode ? (
            <Typography style={{backgroundColor: "#1f1f1f", color: "#efefef"}} component="pre">{code}</Typography>
        ) : null}
      </Paper>
    )
  }
}

export default ProblemView
