import React, { Component } from 'react'
import { Link } from 'react-router-dom';

import Typography from '@material-ui/core/Typography';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import DescriptionIcon from '@material-ui/icons/Description'

import { fetchProblems } from './store/store'

export class Problems extends Component {
  state = {
    loading: true,
    problems: []
  }

  problemsFetched = (problems) => {
    this.setState({
      loading: false,
      problems
    })
  }

  componentDidMount() {
    fetchProblems(this.problemsFetched);
  }

  render() {
    const { loading, problems } = this.state;
    let output = null;
    if (loading)
      output = (<Typography variant="h6">Loading...</Typography>);
    else
      output = (
        <List>
          {problems.map(p => (
          <ListItem component={Link} to={"/problem/"+p.key} key={p.key}>
            <ListItemIcon>
              <DescriptionIcon/>
            </ListItemIcon>
            <ListItemText
              primary={p.name}
              secondary={p.source}/>
          </ListItem>
          ))}
        </List>
      )
    return (
      <div>
        <Typography variant="h2">Problems</Typography>
        {output}
      </div>
    )
  }
}

export default Problems
